
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background-color: white; 
  color: black; 
}

.app {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
}

header {
  text-align: center;
  margin-bottom: 20px;
}

h1 {
  font-size: 2em;
}

.todo-list {
  list-style: none;
  padding: 0;
}

.todo-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

span {
  flex: 1;
}

button {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  color: white;
  background-color: #7B68EE; 
  margin-right: 5px;
}

.add-todo-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white; 
  padding: 20px;
  border: 2px solid #7B68EE;
  width: 300px;
  border-radius: 8px;
  text-align: center;
}

.add-todo-popup input[type="text"] {
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
  border: 1px solid #7B68EE; 
  border-radius: 4px;
}

.popup-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
}

.popup-buttons button {
  flex: 1;
}

.popup-buttons button.cancel-button {
  background-color: white; 
  color: #7B68EE; 
}


.todo-item {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.todo-item label {
  display: flex;
  align-items: center;
  margin-right: 10px; 
}

.todo-item span {
  margin-right: 5px; 
}


input[type="checkbox"] {
  display: none;
}


.checkbox-container {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 20px;
}


.checkbox-container::before {
  content: "";
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 2px solid #7B68EE;
  border-radius: 4px;
}


input[type="checkbox"]:checked + .checkbox-container::before {
  background-color: #7B68EE;
}


.checkbox-container::after {
  content: "\2713"; 
  display: none;
  position: absolute;
  top: 1px;
  left: 3px;
  font-size: 16px;
  color: white;
}


input[type="checkbox"]:checked + .checkbox-container::after {
  display: block;
}


.add-button {
  position: fixed;
  bottom: 20px;
  right: 20px;
  background-color: #7B68EE;
  color: white;
  border: none;
  border-radius: 50%;
  font-size: 24px;
  width: 50px;
  height: 50px;
  cursor: pointer;
}

.filter-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 20px;
}

.filter-section input[type="text"] {
  flex: 2;
  width: 70%; 
  padding: 8px;
  border: 1px solid #7B68EE; 
  border-radius: 4px;
}

.filter-section select {
  flex: .2;
  width: 20%;
  padding: 8px;
  border: 1px solid #7B68EE;
  border-radius: 4px;
  background-color: #7B68EE;
  color: white;
  cursor: pointer;
  margin-left: 10px;
}

.filter-section input[list="filterOptions"] {
  flex: 1;
  width: 30%; 
  padding: 8px;
  border: 1px solid #7B68EE; 
  border-radius: 4px;
  background-color: #7B68EE;
  color: white;
}

.filter-section button {
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  color: white;
  background-color: #7B68EE; 
  margin-left: 10px;
}


